<template>
    <div class="profile">
        <h1>Profile</h1>
    </div>
</template>
<script>
/* eslint-disable no-console */
import * as types from '../../plugins/helper/mutation-types';

export default {
  name: 'profile',
  created() {
    this.checkProfile();
  },
  methods: {
    checkProfile() {
      if (this.$_storage_get(localStorage, types.PROFILE).id) {
        console.log(this.$_storage_get(localStorage, types.PROFILE));
      } else {
        console.log('wrong!!');
        this.axios({
          method: 'post',
          baseURL: types.BASE_URL,
          url: types.API_PROFILE,
          data: { guard: 'api' },
        }).then((response) => {
          console.log('重新請求');
          this.$store.commit(types.SET_PROFILE, response.data);
        }).catch((error) => {
          console.log(error.response.data);
        });
      }
    },
  },
};
</script>
